.NavBarItem {
  fill: var(--white);
  height: 1.25rem;
  width: 1.25rem;
  transition: 0.25s ease;
}

.NavBarLinks {
  color: var(--white);
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  text-decoration: none;
  height: 100%;
  transition: all 0.3s ease;
}
